import Projectile from "./projectile";

export default class Human{
	constructor(environment, context, computerProjectiles){

		this.CONSTANTS = {
      GRAVITY: 0.5
		};

		this.alive = true;

		this.environment = environment;
		this.context = context;

		this.computerProjectiles = computerProjectiles;

		this.projectiles = [];

    this.xPos = 100;
    this.yPos = 100;
    this.yVel = 0;
    this.xVel = 0;
    this.width = 10;
		this.height = 10;
		
		this.onFloor = false;
		this.lastFloorPos = null;
		this.gravityToggle = null;

		this.curPlat = null;

    this.bindMovement();
    this.bindJump();
    this.setClick = this.setClick.bind(this);
    this.setClick(this);
	}
	// ----------------------------------------------------------------------------------------------------
	// ----------------------------------------------------------------------------------------------------
	// ----------------------------------------------------------------------------------------------------
	// ----------------------------------------------------------------------------------------------------
	// ----------------------------------------------------------------------------------------------------
	// ----------------------------------------------------------------------------------------------------
	// ----------------------------------------------------------------------------------------------------
	animate(context) {
		this.move();
		this.draw(context);
		if (this.projectiles.length > 0) {
			this.projectiles.forEach((p) => {
				p.animate(context);
			});
		}
		this.projectiles = this.projectiles.filter(p => p.xPos < 810 && p.xPos > -10 && p.yPos > -10 && p.yPos < 410);
	}

	draw(context) {
		context.fillStyle = 'gray';
		context.fillRect(
			this.xPos, this.yPos, this.width, this.height
		);
	}

	// right now, I am setting yVel to 0 every move when I am on the ground.
	// Instead, I want to set yVel to 0 once, and then once I am on the ground,
	// just not apply gravity
	move() {
		if (this.onFloor !== true) {
			this.yVel += this.CONSTANTS.GRAVITY;
		} else if(this.onFloor === true){
			this.yVel = 0;
			this.yPos = this.curPlat.yStart - 10;
		}
		this.yPos += this.yVel;
		this.xPos += this.xVel;
		if (this.xVel > 0) {
			this.xVel -= 0.1;
		} else if (this.xVel < 0) {
			this.xVel += 0.1;
		}
		debugger;
		this.collidedWithFloor();
		this.getCurrentPlatform();
		debugger;
	}

	// ----------------------------------------------------------------------------------------------------
	// ----------------------------------------------------------------------------------------------------
	// ----------------------------------------------------------------------------------------------------
	// ----------------------------------------------------------------------------------------------------
	// ----------------------------------------------------------------------------------------------------
	// ----------------------------------------------------------------------------------------------------
	// ----------------------------------------------------------------------------------------------------

	collidedWithFloor() {
		debugger;
		if (this.curPlat) {
			if (this.yPos >= this.curPlat.yStart - 10) {
				this.onFloor = true;
			}
		}
	}

	getCurrentPlatform() {
		debugger;
		let that = this;
		for (let i = 0; i < this.environment.platforms.length; i++) {
			let plat = that.environment.platforms[i];
			if (that.xPos > plat.xStart && that.xPos < plat.xStart + plat.width) {
				debugger;
				that.curPlat = plat;
				break;
			} else {
				debugger;
				that.curPlat = null;
				that.onFloor = false;
			}
		}
	}

	// ----------------------------------------------------------------------------------------------------
	// ----------------------------------------------------------------------------------------------------
	// ----------------------------------------------------------------------------------------------------
	// ----------------------------------------------------------------------------------------------------
	// ----------------------------------------------------------------------------------------------------
	// ----------------------------------------------------------------------------------------------------
	// ----------------------------------------------------------------------------------------------------

	bindMovement() {
		window.addEventListener('keypress', (e) => {
			if (e.key === 'd') {
				this.moveRight();
			} else if (e.key === 'a') {
				this.moveLeft();
			}
		});
	}

	bindJump() {
		window.addEventListener('keypress', (e) => {
			if (e.key === 'w') {
				this.jump();
			}
		});
	}

	setClick(that) {
		this.context.canvas.addEventListener('click', (e) => {
			let rect = this.context.canvas.getBoundingClientRect();
			let pos = {};
			pos.x = e.clientX - rect.left;
			pos.y = e.clientY - rect.top;
	
			// debugger;
			that.projectiles.push(new Projectile(
				{ xPos: that.xPos, yPos: that.yPos}, 
				that.context,
				...that.configureProjectile(pos)
				)
			);
		});
	}

	configureProjectile(pos){
		let xDelta = pos.x - this.xPos;
		let yDelta = pos.y - this.yPos;

		let squaredDeltaX = Math.pow(xDelta, 2);
		let squaredDeltaY = Math.pow(yDelta, 2);
		let totalDeltasquared = squaredDeltaX + squaredDeltaY;
		let totalDelta = Math.sqrt(totalDeltasquared);

		let proportion = 20 / totalDelta;
		let xVel = xDelta * proportion;
		let yVel = yDelta * proportion;

		return [xVel, yVel];
	}
	// ----------------------------------------------------------------------------------------------------
	// ----------------------------------------------------------------------------------------------------
	// ----------------------------------------------------------------------------------------------------
	// ----------------------------------------------------------------------------------------------------
	// ----------------------------------------------------------------------------------------------------
	// ----------------------------------------------------------------------------------------------------
	// ----------------------------------------------------------------------------------------------------
	jump(){
		this.onFloor = false;
		this.yVel -= 10;
	}

	moveRight(){
		this.xVel += 2;
		if(this.xVel > 5){
			this.xVel = 5;
		}
	}

	moveLeft() {
		this.xVel -= 2;
		if (this.xVel < -5) {
			this.xVel = -5;
		}
	}

	// ----------------------------------------------------------------------------------------------------
	// ----------------------------------------------------------------------------------------------------
	// ----------------------------------------------------------------------------------------------------
	// ----------------------------------------------------------------------------------------------------
	// ----------------------------------------------------------------------------------------------------
	// ----------------------------------------------------------------------------------------------------
	// ----------------------------------------------------------------------------------------------------
	
}